.toolbar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #ffc922;
    height: 56px;
}

.toolbar-navigation{
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
}

.toolbar-logo {
    margin-left: 1rem;
    vertical-align: middle;
}

.toolbar-logo a {
    color: darkblue;
    text-decoration: none;
    font-size: 1.5rem;
    margin-left: 14px;
    
}

.spacer{
    flex: 1;
}

.toolbar-navigation-items ul{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar-navigation-items li{
    padding: 0 0.5rem;
}

.toolbar-navigation-items a:hover,
.toolbar-navigation-items a:active {
    color: darkgray;
}

@media (max-width: 768px){
    .toolbar-navigation-items {
        display: none;
    }
}
@media(min-width: 769px){
    .toolbar-toggle-button{
        display: none;
    }

    .toolbar-logo{
        margin-left: 0;
    }
}